<template>
  <div class="madar">
    <FilterNav @filterchage="current = $event" :current="current" />
  
    <div v-if="projects.length" class="container">
      <Days @filterchage="days = $event" :days="days" />
  
      <div class="project-list">
        <div v-for="project in filteredProjects" :key="project.id">
          <SingelProject
            :project="project"
            @delete="handleDelete"
            @complete="handleComplete"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { supabase } from '@/supabase'
import SingelProject from '@/components/SingelProject.vue'
import AddProject from './AddProject.vue'
import FilterNav from '@/components/FilterNav.vue'
import Days from '@/components/Days.vue'
import Week from '@/components/Week.vue'

export default {
  name: 'Home',
  components: { SingelProject, AddProject, FilterNav, Days,Week },
  data() {
    return {
      projects: [],
      current: 'all',
      days: 'one'
    }
  },
  async mounted() {
    const { data, error } = await supabase
      .from('projects')
      .select('*')
    if (error) {
      console.error('خطا در دریافت برنامه ها:', error)
    } else {
      this.projects = data
    }
  },
  methods: {
    async handleDelete(id) {
      const { error } = await supabase
        .from('projects')
        .delete()
        .eq('id', id)
      if (!error) {
        this.projects = this.projects.filter(item => item.id !== id)
      } else {
        console.error('خطا در حذف برنامه:', error.message)
      }
    },
    async handleComplete(id) {
      let project = this.projects.find(item => item.id === id)
      if (!project) return

      const { error } = await supabase
        .from('projects')
        .update({ complete: !project.complete })
        .eq('id', id)

      if (!error) {
        project.complete = !project.complete
      } else {
        console.error('خطا در تغییر وضعیت برنامه:', error.message)
      }
    }
  },
  computed: {
    filteredProjects() {
      return this.projects
        .filter(project => {
          if (this.current === 'completed') return project.complete
          if (this.current === 'ongiong') return !project.complete
          return true
        })
        .filter(project => project.day === this.days)
    }
  }
}
</script>

<style>
.container {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}

.project-list {
  flex-grow: 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
    align-items: center;
  }
}
</style>
