<template dir="rtl">
  <div class="madars">
    <form @submit.prevent="handleSubmit">
      <label>موضوع:</label>
      <input v-model="title" type="text" required />

      <label>جزئیات:</label>
      <textarea v-model="details"></textarea>

      <label>روز:</label>
      <select v-model="day">
        <option value="one">روز اول</option>
        <option value="two">روز دوم</option>
        <option value="three">روز سوم</option>
      </select>

      <button>بروزرسانی برنامه</button>
    </form>
  </div>
</template>

<script>
import { supabase } from '@/supabase'

export default {
  props: ['id'],
  data() {
    return {
      title: '',
      details: '',
      day: '',
    }
  },
  async mounted() {
    const { data, error } = await supabase
      .from('projects')
      .select()
      .eq('id', this.id)
      .single()

    if (error) {
      console.error('خطا در دریافت اطلاعات پروژه:', error.message)
      alert('خطا در بارگذاری پروژه!')
    } else {
      this.title = data.title
      this.details = data.details
      this.day = data.day
    }
  },
  methods: {
    async handleSubmit() {
      const { error } = await supabase
        .from('projects')
        .update({
          title: this.title,
          details: this.details,
          day: this.day,
        })
        .eq('id', this.id)

      if (error) {
        console.error('خطا در بروزرسانی پروژه:', error.message)
        alert('خطا در بروزرسانی پروژه! لطفاً دوباره تلاش کنید.')
      } else {
        this.$router.push('/')
      }
    },
  },
}
</script>

<style>
.madars {
  display: flex;
  justify-content: center;
  padding: 20px;
}

form {
  background: #b0db9c;
  padding: 20px;
  border-radius: 10px;
  width: 60%;
  margin: 0 auto;
}

label {
  display: block;
  color: #555;
  font-weight: bold;
  font-size: 14px;
  margin: 20px 0 10px 0;
  text-transform: uppercase;
  letter-spacing: 1px;
}

input,
textarea,
select {
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #ddd;
  width: 100%;
  box-sizing: border-box;
  background: #ddf6d2;
}

textarea {
  height: 150px;
  resize: vertical;
}

select {
  width: auto;
  min-width: 120px;
  text-align: center;
}
</style>
