<template>
  <div class="madars">
    <form @submit.prevent="handleSubmit">
      <label>موضوع:</label>
      <input v-model="title" type="text" required />
      <label>جزئیات:</label>
      <textarea v-model="details"></textarea>
      <label>انتخاب روز:</label>
      <select v-model="day">
        <option value="one">روز اول</option>
        <option value="two">روز دوم</option>
        <option value="three">روز سوم</option>
      </select>
      <button>اضافه کردن برنامه</button>
    </form>
  </div>
</template>

<script>
import { supabase } from '@/supabase'

export default {
  data() {
    return {
      title: '',
      details: '',
      day: 'one',
    }
  },
  methods: {
    async handleSubmit() {
      const project = {
        title: this.title,
        details: this.details,
        day: this.day,
        complete: false,
      }

      const { data, error } = await supabase
        .from('projects')
        .insert([project])

      if (error) {
        console.error('خطا در اضافه کردن پروژه:', error.message)
        alert('خطا در ثبت پروژه! لطفاً دوباره تلاش کنید.')
      } else {
        // بعد از موفقیت، صفحه اصلی یا هر صفحه‌ای که می‌خوای برو
        this.$router.push('/')
      }
    },
  },
}
</script>

<style>
.madars {
  display: flex;
  justify-content: center;
  padding: 20px;
}

form {
  background: #b0db9c;
  padding: 20px;
  border-radius: 10px;
  width: 60%;
  margin: 0 auto;
}

label {
  display: block;
  color: #555;
  font-weight: bold;
  font-size: 14px;
  margin: 20px 0 10px 0;
  text-transform: uppercase;
  letter-spacing: 1px;
}

input,
textarea,
select {
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #ddd;
  width: 100%;
  box-sizing: border-box;
  background: #ddf6d2;
}

textarea {
  height: 150px;
  resize: vertical;
}

select {
  width: auto;
  min-width: 120px;
  text-align: center;
}

.madars button {
  display: block;
  background: #4caf50;
  padding: 10px;
  border-radius: 10px;
  border: none;
  color: white;
  cursor: pointer;
  width: 70%;
  margin-top: 20px;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;
}

.madars button:hover {
  background: #4c8f4e;
  transform: scale(1.1);
}
</style>
